		return{
			imglogo:COMPANY_LOGO
		}
	},
	methods:{
		submitBtn(){
			
			uni.navigateTo({
				url:UPLOAD
			})
			
		},
		apptMeasureHomeBtn(){
			uni.navigateTo({
				url:INVITE
			})
		}
	},
	async onLoad() {}
};
